  async nullDeviceSeed(emailAddress) {
      this.sodium = (!this.sodium) ? await SodiumPlus.auto() : this.sodium;
      var didIdentifer = await this.sodium.crypto_generichash(emailAddress, null, 16);
      this.store.setItem("__did_" + didIdentifer.toString('hex'), "");
      this.isNewBrowser = true;
  }

  async getDeviceSeed(emailAddress) {
      this.sodium = (!this.sodium) ? await SodiumPlus.auto() : this.sodium;
      var didIdentifer = await this.sodium.crypto_generichash(emailAddress, null, 16);

      if (this.store.getItem("__did_" + didIdentifer.toString('hex'))) {
          this.isNewBrowser = false;
          return {
              "seed": this.store.getItem("__did_" + didIdentifer.toString('hex'))
          };
      } else {
          var deviceSeed = await this.sodium.randombytes_buf(32);
          var deviceSeed = await this.sodium.crypto_generichash(emailAddress + ":" + deviceSeed);
          this.store.setItem("__did_" + didIdentifer.toString('hex'), deviceSeed.toString('hex'))
          this.isNewBrowser = true;
          return {
              "seed": deviceSeed.toString('hex')
          };
      }

      // COOKIE VERSION
      // var body = {"emailAddress":emailAddress};
      // return this.httpClient.post(environment.API_ENDPOINT + 'v1/public/getDeviceSeed', body, {withCredentials: true, responseType: 'json'});
  }

