  async generateMQTTKeys(credentials) {
      this.sodium = (!this.sodium) ? await SodiumPlus.auto() : this.sodium;
      var deviceSeed: any = await this.getDeviceSeed(credentials.email);
      if (!deviceSeed.seed) {
          throw new Error("Could not fetch device seed");
      }

      deviceSeed = Buffer.from(deviceSeed["seed"], 'hex');

      var deviceEncryptionKeyPair = await this.sodium.crypto_kx_seed_keypair(deviceSeed);
      var deviceEncryptionPrivateKey = await this.sodium.crypto_box_secretkey(deviceEncryptionKeyPair);
      let deviceEncryptionPublicKey = await this.sodium.crypto_box_publickey(deviceEncryptionKeyPair);
      var deviceSigningKeyPair = await this.sodium.crypto_sign_seed_keypair(deviceSeed);
      let deviceSigningPrivateKey = await this.sodium.crypto_sign_secretkey(deviceSigningKeyPair);
      let deviceSigningPublicKey = await this.sodium.crypto_sign_publickey(deviceSigningKeyPair);

      var deviceKeys = {
          "encryptPublicKey": deviceEncryptionPublicKey.toString('base64'),
          "encryptPrivateKey": deviceEncryptionPrivateKey.toString('base64'),
          "signPublicKey": deviceSigningPublicKey.toString('base64'),
          "signPrivateKey": deviceSigningPrivateKey.toString('base64'),
          "username": deviceSigningPublicKey.toString('hex')
      }

      return deviceKeys;
  };

  signout() {
      // Disconnect from API
      this.WSS.disconnectAPI();

      // Delete all user profile objects
      this.userSettings = {};
      this.deviceSettings = {};
      this.jemService.jems.next([]);

      // Wipe the vault and clear vaultLoaded flag
      this.vault.next([]);
      this.vaultLoaded = false;

      // Set authenticated to false
      this.authenticated = false;

      // Delete all localStorage entries
      this.store.setItem("loggedInUser", false);
      this.store.setItem("emailAddress", false);
      this.store.setItem("deviceSettings", false);
      this.store.setItem("userSettings", false);

      // Used to router.navigate - but favour window.location to ensure all params reset
      window.location.assign("/signin");
  }

  async isSignatureValid(message, pubKey) {
      pubKey = pubKey.key;
      this.sodium = (!this.sodium) ? await SodiumPlus.auto() : this.sodium;
      var msgBuf = Buffer.from(message.substring(0, 16), 'hex');
      var signature = Buffer.from(message.substring(16, message.length), 'hex');
      pubKey = Ed25519PublicKey.from(pubKey, 'base64');
      if (await this.sodium.crypto_sign_verify_detached(msgBuf, pubKey, signature)) {
          return true;
      } else {
          return false;
      }
  }

