  async loadVault(authToken) {

      this.vaultLoaded = true;

      var parent = this;

      this.WSS.bind('getAllCredentials_RESPONSE', async function(response) {
          this.vaultLoaded = true;
          var sodium = (!sodium) ? await SodiumPlus.auto() : sodium;

          switch (response.type) {
              case "add":
                  var data = JSON.parse(response.new_data.data);
                  var metaKEKBuf = Buffer.from(data.metaKEK, 'base64');
                  var metaDataBuf = Buffer.from(data.metaData, 'base64');

                  var metaKey = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaKEKBuf.slice(24),
                      metaKEKBuf.slice(0, 24),
                      CryptographyKey.from(parent.userSettings.metaKey)
                  );

                  var metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaDataBuf.slice(24),
                      metaDataBuf.slice(0, 24),
                      CryptographyKey.from(metaKey)
                  );

            metaData = JSON.parse(metaData);
            parent.vault.next([...parent.vault.value, {
                "id": response.new_data.RowKey,
                "title": metaData.title,
                "url": metaData.url,
                "username": metaData.username,
                "status": "encrypted",
                "passKEK": data.passKEK,
                "passData": data.passData,
                "decryptedPassword": '',
                'eraseDecryptedDataTimeout': null,
                'clearEraseDecryptedDataTimeout': function() {
                  if (this.eraseDecryptedDataTimeout) clearTimeout(this.eraseDecryptedDataTimeout);
                  this.eraseDecryptedDataTimeout = null;
                },
                'eraseDecryptedData': function(ms) {
                  this.eraseDecryptedDataTimeout = setTimeout(async () => {
                        console.log(`erasing decrypted data for ${this.title}`);
                        this.decryptedPassword = await sodium.randombytes_buf(32);
                        this.decryptedPassword = null;
                        this.status = 'encrypted';
                    }, ms);
                },
            }]);
          break;

              case "remove":
                  parent.vault.next(parent.removeByAttr(parent.vault.value, 'id', response.old_data.RowKey));
                  break;

              case "change":
                  parent.vault.next(parent.removeByAttr(parent.vault.value, 'id', response.old_data.RowKey));
                  var data = JSON.parse(response.new_data.data);
                  var metaKEKBuf = Buffer.from(data.metaKEK, 'base64');
                  var metaDataBuf = Buffer.from(data.metaData, 'base64');

                  var metaKey = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaKEKBuf.slice(24),
                      metaKEKBuf.slice(0, 24),
                      CryptographyKey.from(parent.userSettings.metaKey)
                  );

                  var metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaDataBuf.slice(24),
                      metaDataBuf.slice(0, 24),
                      CryptographyKey.from(metaKey)
                  );

            metaData = JSON.parse(metaData);
            parent.vault.next([...parent.vault.value, {
                "id": response.new_data.RowKey,
                "title": metaData.title,
                "url": metaData.url,
                "username": metaData.username,
                "status": "encrypted",
                "passKEK": data.passKEK,
                "passData": data.passData,
                "decryptedPassword": '',
                'eraseDecryptedDataTimeout': null,
                'clearEraseDecryptedDataTimeout': function() {
                  if (this.eraseDecryptedDataTimeout) {
                    clearTimeout(this.eraseDecryptedDataTimeout);
                  }
                  this.eraseDecryptedDataTimeout = null;
                },
                'eraseDecryptedData': function(ms) {
                  this.clearEraseDecryptedDataTimeout();
                  this.eraseDecryptedDataTimeout = setTimeout(async () => {
                        console.log(`erasing decrypted data for ${this.title}`);
                        this.decryptedPassword = await sodium.randombytes_buf(32);
                        this.decryptedPassword = null;
                        this.status = 'encrypted';
                    }, ms);
                },
            }]);
          break;

              case "initial":
                  // initial data?
                  var data = JSON.parse(response.new_data.data);
                  var metaKEKBuf = Buffer.from(data.metaKEK, 'base64');
                  var metaDataBuf = Buffer.from(data.metaData, 'base64');

                  var metaKey = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaKEKBuf.slice(24),
                      metaKEKBuf.slice(0, 24),
                      CryptographyKey.from(parent.userSettings.metaKey)
                  );

                  var metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                      metaDataBuf.slice(24),
                      metaDataBuf.slice(0, 24),
                      CryptographyKey.from(metaKey)
                  );

            metaData = JSON.parse(metaData);
            parent.vault.next([...parent.vault.value, {
                "id": response.new_data.RowKey,
                "title": metaData.title,
                "url": metaData.url,
                "username": metaData.username,
                "status": "encrypted",
                "passKEK": data.passKEK,
                "passData": data.passData,
                "decryptedPassword": '',
                'eraseDecryptedDataTimeout': null,
                'clearEraseDecryptedDataTimeout': function() {
                  if (this.eraseDecryptedDataTimeout) {
                    clearTimeout(this.eraseDecryptedDataTimeout);
                  }
                  this.eraseDecryptedDataTimeout = null;
                },
                'eraseDecryptedData': function(ms) {
                    this.clearEraseDecryptedDataTimeout();
                    this.eraseDecryptedDataTimeout = setTimeout(async () => {
                        console.log(`erasing decrypted data for ${this.title}`);
                        this.decryptedPassword = await sodium.randombytes_buf(32);
                        this.decryptedPassword = null;
                        this.status = 'encrypted';
                    }, ms);
                },
            }]);
          break;

              case "uninitial":
                  // do nothing
                  break;

              case "state":

                  break;
          }
      });

      this.WSS.API('getAllCredentials', {
          auth: authToken
      });

      this.WSS.event.on('socket_reconnected', () => {
          this.vault.next([]);
          this.WSS.API('getAllCredentials', {
              auth: authToken
          });
      })
  }

