import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SodiumPlus, X25519PublicKey, X25519SecretKey, CryptographyKey } from 'sodium-plus';
import { JemService } from '../../shared/services/jem.service';
import { AuthService } from '../../shared/services/auth.service';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';


interface Credential {
  id: String;
  title: String;
  url: String;
  username: String;
  status: String;
  password: String;
}

/**
 * @author Amitav Nott
 * @since 6/28/2020
 * @version 1.0
 * The content displayed within the modal that shows the details of a given credential
 */
@Component({
  selector: 'app-detail-in-modal',
  templateUrl: './detail-in-modal.component.html',
  styleUrls: ['./detail-in-modal.component.scss']
})
export class DetailInModalComponent {
  @Input() credential: Credential;
  loading = false;
  loadingText;
  viewMode: 'edit' | 'print' = 'print';
  id: String;
  isNew: boolean;
  vault: any = {};
  vaultForm: FormGroup;

  constructor(
    public activeModal: NgbActiveModal,
    private jemService: JemService,
    private auth: AuthService,
    private fb: FormBuilder,
    private toastr: ToastrService

  ) { }
  
  
  ngOnInit() {
    this.id = this.credential.id;
    this.isNew = !this.id;
    this.buildVaultForm(this.credential);
  }

  
  async decrypt(credential, passKEK, passData) {
    this.loadingText = "Press your JEM...";
    this.loading = true;
    // this.loadingText[credential.id] = "Press your JEM...";
    // this.loading[credential.id] = true;
    var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    var passKEKBuf = Buffer.from(passKEK, 'base64');
    var passKeyB64;

    try {
      await this.jemService.decrypt(passKEKBuf, this.auth.deviceSettings)
          .then(async passKey => {
        passKeyB64 = passKey;
      });
    }
    catch(error) {
      this.loading[credential.id] = false;
      this.toastr.error(error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }
    var passDataBuf = Buffer.from(passData, 'base64');
    
    var passData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
    passDataBuf.slice(24),
    passDataBuf.slice(0, 24),
    CryptographyKey.from(passKeyB64, 'base64'));

    var passData = JSON.parse(passData.toString());
    credential.status = 'decrypted';
    credential.password = passData.password;
    this.loading[credential.id] = false;
  }

  isMobile() {
    return window.innerWidth < 600;
  }

  buildVaultForm(i: any = {}) {
    this.vaultForm = this.fb.group({
        id: [i.id],
        title: [i.title],
        url: [i.url],
        username: [i.username],
        status: [i.status], 
        password: [i.password],
    });
  }

  createItem(item: any = {}) {
    return this.fb.group({
        title: [item.title],
        url: [item.url],
        username: [item.username],
        password: [item.password]
    });
}

async saveItem() {
    this.loading = true;
    this.loadingText = "Touch your JEM...";
    var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    this.vault = this.vaultForm.value;
    var metaKey = await sodium.randombytes_buf(32);
    var passKey = await sodium.randombytes_buf(32);

    var record:any = {
        metaKEK: "",
        passKEK: "",
        type: 1,
        metaData: {
            "title": this.vault.title,
            "username": this.vault.username,
            "url": this.vault.url
        },
        passData: {
            "password": this.vault.password
        }
    };

    // ENCRYPT METADATA
    var nonce = await sodium.randombytes_buf(24);
    record.metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
        JSON.stringify(record.metaData),
        nonce,
        CryptographyKey.from(metaKey)
    );
    record.metaData = Buffer.concat([nonce, record.metaData]);
    record.metaData = record.metaData.toString('base64');

    // ENCRYPT PASSDATA
    var nonce = await sodium.randombytes_buf(24);
    record.passData = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
        JSON.stringify(record.passData),
        nonce,
        CryptographyKey.from(passKey)
    );
    record.passData = Buffer.concat([nonce, record.passData]);
    record.passData = record.passData.toString('base64');

    // ENCRYPT METAKEY
    var nonce = await sodium.randombytes_buf(24);
    record.metaKEK = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
        metaKey,
        nonce,
        CryptographyKey.from(this.auth.userSettings.metaKey)
    );
    record.metaKEK = Buffer.concat([nonce, record.metaKEK]);
    record.metaKEK = record.metaKEK.toString('base64');

    try {
      await this.jemService.encrypt(passKey, this.auth.deviceSettings, true)
      .then(data => {
          record.passKEK = data;
      });
    }
    catch(error) {
      this.loading = false;
      this.toastr.error(error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }

    this.auth.addCredential(record)
        .subscribe((item: any) => {
            this.toastr.success('Item Saved!', 'Success!', { timeOut: 3000 });
            this.activeModal.close('OK');
        });
}

}