import { Component, OnInit } from '@angular/core';
import { Observable, combineLatest } from 'rxjs';
import { FormControl } from '@angular/forms';
import { startWith, debounceTime, switchMap, map } from 'rxjs/operators';
import { SharedAnimations } from '../../animations/shared-animations';
import { SearchService } from '../../services/search.service';
import { SodiumPlus, CryptographyKey } from 'sodium-plus';
import { JemService } from '../../../shared/services/jem.service';
import { AuthService } from '../../../shared/services/auth.service';

interface Credential {
  id: String;
  passData: String;
  passKEK: String;
  status: String;
  title: String;
  username: String;
}

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],
  animations: [SharedAnimations]
})
export class SearchComponent implements OnInit {
  page = 1;
  pageSize = 6;
  results$: Observable<any[]>;
  searchCtrl: FormControl = new FormControl('');
  localVault: any; // an observable that when subscribed to emits the vault

  constructor(
    public searchService: SearchService,
    private auth: AuthService,
    private jemService: JemService
  ) { }

  async ngOnInit() {
    await this.loadVault();

    this.results$ = combineLatest(
      this.localVault,
      this.searchCtrl.valueChanges
      .pipe(startWith(''), debounceTime(200))
    )
    .pipe(map(([credentials, searchTerm]: [Credential[], String]) => {
      /* TODO: fix this hack (figure out why this.results$ is empty
      at initialization of search component) */
      if (searchTerm === '') {
        return credentials;
      }
      return credentials.filter(credential => {
        return credential.title.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
      });
    }));
  }

  async loadVault() {
    var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    var outputVault = [];
        await this.auth.getAllCredentials()
            .subscribe(async res => {
        res.forEach(async item => {
          var data = JSON.parse(item.data._);
          var metaKEKBuf = Buffer.from(data.metaKEK, 'base64');
          var metaDataBuf = Buffer.from(data.metaData, 'base64');

          var metaKey = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
            metaKEKBuf.slice(24),
            metaKEKBuf.slice(0, 24),
            CryptographyKey.from(this.auth.userSettings.metaKey)
          );

          var metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
            metaDataBuf.slice(24),
            metaDataBuf.slice(0, 24),
            CryptographyKey.from(metaKey)
          );

          metaData = JSON.parse(metaData);

          outputVault.push({
            "id":item.RowKey._,
            "title":metaData.title,
            "url":metaData.url,
            "username":metaData.username,
            "status": "encrypted",
            "passKEK": data.passKEK,
            "passData": data.passData
          });
               });
      });
      // this.localVault = outputVault;
      // there is no need for the local vault not as an observable
      this.localVault = new Observable(subscriber => {
        subscriber.next(outputVault);
      });
  }
}
