// For Platform: Desktop - Web

import { Component, OnInit, Input } from '@angular/core';
import { Observable, combineLatest } from 'rxjs';
import { FormControl } from '@angular/forms';
import { startWith, debounceTime, switchMap, map } from 'rxjs/operators';
import { SharedAnimations } from '../../animations/shared-animations';
import { SodiumPlus, CryptographyKey } from 'sodium-plus';
import { JemService } from '../../services/jem.service';
import { AuthService } from '../../services/auth.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { CredDetailInModalComponent } from '../cred-detail-in-modal/cred-detail-in-modal.component';
import { SearchService } from 'src/app/shared/services/search.service';

interface Credential {
  id: string;
  passData: string;
  passKEK: string;
  status: string;
  title: string;
  username: string;
  url: string;
  tags: string[];
  // favorite: boolean;
}

@Component({
  selector: 'app-stacked-divs',
  templateUrl: './stacked-divs.component.html',
  styleUrls: ['./stacked-divs.component.scss'],
  animations: [SharedAnimations],
})
export class StackedDivsComponent implements OnInit {
  page = 1;
  pageSize = 6;
  results$: Observable<any[]>;
  searchCtrl: FormControl = new FormControl('');
  localVault: any; // an observable that when subscribed to emits the vault
  searching: boolean;
  loading: boolean[];
  loadingText: string[];
  @Input() viewPortOverlay: boolean;
  filterFavorites = false;
  inputText: String;
  decryptedCredentials: any;
  viewMode: 'grid' | 'list' = 'list';
  showDecryptedPassword: boolean[] = []; 
  copyToClipboardButtonText: { username: string, password: string } = {
    username: "Copy",
    password: "Copy"
  }; 

  vaultLoaded: boolean = false;
  showVaultIsEmptyDiv: boolean = false; 
  showSearchInputField: boolean = true; 

  constructor(
    private auth: AuthService,
    private jemService: JemService,
    private toastr: ToastrService,
    private modalService: NgbModal,
    public searchService: SearchService,
  ) { }

  async ngOnInit() {
    this.searching = false;
    this.loading = [];
    this.loadingText = [];
    this.filterSearchResults();

    this.auth.vault.subscribe(data => {
      this.searchCtrl.setValue(this.searchCtrl.value); 

      this.vaultLoaded = false; 
      this.showSearchInputField = true; 
      this.showVaultIsEmptyDiv = (data.length > 2) ? false : true; 
      setTimeout (() => {
        this.vaultLoaded = true; 
        this.showSearchInputField = !this.showVaultIsEmptyDiv;
      }, 1000);
    });
  }

  filterSearchResults() {
    this.results$ = combineLatest(
      [
        this.auth.vault,
        this.searchCtrl.valueChanges
        .pipe(startWith(''), debounceTime(200)),
      ]
    )
    .pipe(map(([credentials, searchTerm]: [Credential[], String]) => {
      let matchingCredentials: any[];
      this.page = 1;
      /* TODO: fix this hack (figure out why this.results$ is empty
      at initialization of search component) */
      if (searchTerm === '') {
        matchingCredentials = credentials;
        this.searching = false;
      } else {
        matchingCredentials = credentials.filter(credential => {
          this.searching = true;
          let titleMatch = false;
          let urlMatch = false;
          let tagMatch = false;
          let favoriteMatch = false;

          if (credential.title) {
            titleMatch = credential.title.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
          }
          if (credential.url) {
              urlMatch = credential.url.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
          }
          if (credential.tags) {
            for (let i = 0; i < credential.tags.length; i++) {
              if (credential.tags[i].toLowerCase().indexOf(searchTerm.toLowerCase()) > -1) {
                if (credential.tags[i] === '_favorite') {
                  favoriteMatch = true;
                }
                tagMatch = true;
                break;
              }
            }
          }

          if (this.filterFavorites) {
            return favoriteMatch && (titleMatch || urlMatch || tagMatch);
          }
          return titleMatch || urlMatch || tagMatch;
        });
      }
      return matchingCredentials;
    }));
  }

  async decrypt(credential, passKEK, passData) {
    this.loadingText[credential.id] = "Press your JEM...";
    this.loading[credential.id] = true;
    var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    var passKEKBuf = Buffer.from(passKEK, 'base64');
    var passKeyB64;

    try {
      await this.jemService.decrypt(passKEKBuf, this.auth.deviceSettings)
          .then(async passKey => {
        passKeyB64 = passKey;
      });
    }
    catch(error) {
      this.loading[credential.id] = false;
      this.toastr.error(error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }
    var passDataBuf = Buffer.from(passData, 'base64');
 
    var passData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
    passDataBuf.slice(24),
    passDataBuf.slice(0, 24),
    CryptographyKey.from(passKeyB64, 'base64'));

    var passData = JSON.parse(passData.toString());
    credential.status = 'decrypted';
    credential.decryptedPassword = passData.password;
    this.loading[credential.id] = false;

    credential.eraseDecryptedData(10000);
    setTimeout(_ => this.showDecryptedPassword[credential.id] = false, 10000)
  }


  deleteItem(id, modal) {
    this.modalService.open(modal, { ariaLabelledBy: 'modal-basic-title', centered: true })
      .result.then((result) => {
          this.auth.deleteCredential(id)
              .subscribe(res => {
                  this.toastr.success('Item Deleted!', 'Success!', { timeOut: 3000 });
                  this.filterSearchResults();
              })
      }, (reason) => {
      });
    }

  openDetailModal(id: String, mode: "edit | view" ) {
    const element = document.querySelector('app-stacked-divs');
    element.scrollIntoView();

    const modalRef = this.modalService.open(CredDetailInModalComponent);
    const matchingCredential = this.auth.vault.value.filter(credential => credential.id === id);
    if (matchingCredential.length > 1) {
      console.error(`multiple credentials with id ${id}`);
    } else {
      modalRef.componentInstance.credential = matchingCredential[0];
      modalRef.componentInstance.viewMode = mode;
    }
  }

  isMobile(): boolean {
    return window.innerWidth < 600;
  } 

  handleBtnLoadingClick(credential) {
    this.showDecryptedPassword[credential.id] = true;
    this.decrypt(credential, credential.passKEK, credential.passData)
  }

  handleDecryptedPasswordClick(credential) {
    this.showDecryptedPassword[credential.id] = false;
    credential.eraseDecryptedData(0);
  }

  private copy(item: string) {
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = item;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    // this.toastr.success('The password is in your clipboard!', 'Success!', { timeOut: 5000 });
  }

  copyToClipboard(itemToCopy: string, textToCopy: string) {
    this.copy(textToCopy); 
    if (itemToCopy == 'username') {
      this.copyToClipboardButtonText.username = "Copied"; 
      setTimeout(() => {
        this.copyToClipboardButtonText.username = "Copy";
      }, 2000);
    } else {
      this.copyToClipboardButtonText.password = "Copied"; 
      setTimeout(() => {
        this.copyToClipboardButtonText.password = "Copy";
      }, 2000);
    }
  }
}
