import { Component, OnInit } from "@angular/core";
import { NavigationService } from "src/app/shared/services/navigation.service";
import { SearchService } from "src/app/shared/services/search.service";
import { AuthService } from "src/app/shared/services/auth.service";
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: "app-header-sidebar-compact",
  templateUrl: "./header-sidebar-compact.component.html",
  styleUrls: ["./header-sidebar-compact.component.scss"]
})
export class HeaderSidebarCompactComponent implements OnInit {
  notifications: any[];

  constructor(
    private navService: NavigationService,
    public searchService: SearchService,
    public auth: AuthService,
    private toastr: ToastrService, 
    private router: Router
  ) {
    this.notifications = [];
  }

  ngOnInit() {
    this.auth.sessionTime.on("sessionTime", time => {
      switch(time) {
        case 60:
          this.toastr.success('Session ends in 60 seconds', 'Session Update', { timeOut: 3000 });
        break;

        case 30:
          this.toastr.warning('Session ends in 30 seconds', 'Session Update', { timeOut: 3000 });
        break;

        case 10:
          this.toastr.error('Session ends in 10 seconds', 'Session Update', { timeOut: 3000 });
        break;
      }
    })
  }

  toggelSidebar() {
    const state = this.navService.sidebarState;
    state.sidenavOpen = !state.sidenavOpen;
    state.childnavOpen = !state.childnavOpen;
  }

  signout() {
    this.auth.signout();
  }

  stackedDivsOnPage() {
    return document.querySelector('app-stacked-divs') !== null;
  }
}
