'use strict';
import {
    Injectable
} from "@angular/core";
import {
    SodiumPlus,
    X25519PublicKey,
    X25519SecretKey
} from 'sodium-plus';
import {
    EventEmitter
} from 'events';
import { environment } from 'src/environments/environment';
import base64url from "base64url";
import {
    BehaviorSubject
} from "rxjs";
import * as paseto from 'paseto.js';
import { WebSocketService } from './websocket'

let sodium;
const protobuf = require('protobufjs/light.js');
const jemNGjson = require('../../../../src/blecomm.json');
const jemNGRoot = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG");
const jemNGPR = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_ProvisionRsp");
const jemNGEnc = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_Encrypted_API_Msg");
const jemNGER = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_EncryptKeyRsp");
const jemNGDR = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_DecryptKeyRsp");
const jemNGPRR = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_ProvisionRecoveryRsp");
const jemNGACVR = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_AddClientVerifyRsp");
const jemNGPsR = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_PasetoRsp");
const jemEnrollReqEnum = jemNGRoot.lookup("EnrollReqEnum").values;
let V2 = new paseto.V2();

@Injectable({
    providedIn: 'root',
})
export class JemService {
    constructor(private WSS: WebSocketService) {}

    public serviceEmitter = new EventEmitter();
    public jems: BehaviorSubject<any> = new BehaviorSubject({});
    public jemStatusListening = false;
    public firmwareUpgraded = false;
    public jemFound = false;
    public locked = false;

    // -- CORE FUNCTIONS

    async waitForJem(jemID=null) {

        var parent = this;

        return new Promise(async (resolve, reject) => {

            if(!parent.locked)
            {
                parent.locked = true;
                if(!this.jemStatusListening) {
                    this.jemStatusListening = true;
                    this.WSS.bind('JEMSTATUS', function(jem) {
                        parent.jems.value.find(o => o.jemID === jem.jemID).status = jem.status;
                        parent.jems.value.find(o => o.jemID === jem.jemID).firmwareVersion = jem.firmwareVersion;
                        parent.jems.next(parent.jems.value);
                    });
                }

                    setTimeout(function() {
                        parent.locked = false;
                        parent.jemStatusListening = false;
                        reject("No online JEM found.  You must press your JEM within 25 seconds.")
                    }, 25000);

                    this.jems.subscribe(jem => {

                        if(this.jems.value.length > 0) {
                            if(jem[0].status == "online") {
                                this.serviceEmitter.emit('JEMONLINE');
                                resolve(jem[0].jemID);
                            }
                        }
                    })

                    // JEM is already online from registration, resolve immediately.
                    if(jemID) {
                        this.jems.value.find(o => o.jemID === jemID).status = "online";
                        this.jems.next(this.jems.value);
                    }
            }
            else{
                reject("The app is busy. Please wait...")
            }
            });
    } 

    async provisionFirstJEM(devID, username, emailAddress, deviceKeys, recoveryKey) {

        return new Promise<any>(async (resolve, reject) => {

            var parent = this;

            var jemPublicKey;
            var provisionResponse;

            this.WSS.API('SUBSCRIBE_TO_JEM', {
                jemID: devID
            })

            this.WSS.bind('JEMSTATUS', async function(jem) {
                switch (jem.status) {
                    case "PROVISION_NO_FINGER":

                        if(jem.firmwareVersion)
                        {
                            var firmwareVersion = jem.firmwareVersion.substr(4);
                        }

                        if(firmwareVersion == "1.0.10") parent.firmwareUpgraded = true;
                        if(firmwareVersion == "1.0.10-devapi") parent.firmwareUpgraded = true;

                        if(!parent.firmwareUpgraded) {
                            
                            setTimeout(function() {
                                if(!parent.jemFound) {
                                    reject("No JEM found for firmware update.  You must press your JEM within 25 seconds, check your JEM ID and ensure your jemPass application is running.")
                                }
                            }, 25000);
                            
                            parent.serviceEmitter.emit('JEMSTATUS', "FIRMWARE_UPGRADE");
                            await parent.DFUUpdate(devID, deviceKeys);
                        }
                        else {
                            parent.firmwareUpgraded = false;
                            parent.serviceEmitter.emit('JEMSTATUS', "PROVISION_NO_FINGER");
                            parent.sendEnrollReq(devID, "device/" + username + "/response");
                        }
                        break;

                    case "PROVISION_FINGER":
                        parent.serviceEmitter.emit('JEMSTATUS', "PROVISION_FINGER");
                        parent.sendProvisionReq(devID, "device/" + username + "/response", emailAddress, Buffer.from(deviceKeys.encryptPublicKey, 'base64'), 86400);
                        break;

                    case "offline":
                        parent.serviceEmitter.emit('JEMSTATUS', "offline");
                    break;
                }
            });
        
            this.WSS.bind('SEND_PROVISION_RESPONSE', function(message) {
                var decodedMessage = jemNGRoot.fromObject(message);
                if (decodedMessage.api.provisionRsp) {
                    parent.handleProvisioningResponse(decodedMessage.api.provisionRsp, X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                        .then(async rsp => {
                            if (rsp == null) {
                                reject({message: "Received in invalid provisioning response"});
                            } else {
                                jemPublicKey = rsp.jemPublicKey;
                                provisionResponse = rsp.provisionResponse;
                                parent.sendProvisionRecReq(devID, "device/" + username + "/response", recoveryKey, rsp.jemPublicKey, X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                            }
                        });
                }
            });
                
            this.WSS.bind('SEND_PROVISION_REC_RESPONSE', function(message) {
                var decodedMessage = jemNGRoot.fromObject(message);
                if (decodedMessage.api.provisionRecRsp) {
                    parent.handleRspProvisioningRec(decodedMessage.api.provisionRecRsp, jemPublicKey, X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                        .then(rsp => {
                            if (rsp == null) {
                                reject({message: "Received in invalid recovery key response"});
                            } else {
                                resolve({provisionResponse: provisionResponse, encryptionPrivateKeyKEK: rsp.userEncPrivKeyEnc, signingPrivateKeyKEK: rsp.userSigPrivKeyEnc})
                            }
                        });
                }
            });
        });
    } 

    async fetchPasetoLoginToken(sessionDuration, deviceKeys, userSigningPublicKey, jemID=null) {

        return new Promise(async (resolve, reject) => {

                try {
                    jemID = await this.waitForJem(jemID);
                }
                catch(error){
                    return reject({message: error})
                }

                // SEND PASETO REQUEST TO JEM
                await this.sendPasetoReq(jemID, "device/" + deviceKeys.username + "/response", false, "my.jempass.com", parseInt(sessionDuration), 1, X25519PublicKey.from(this.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'));

                var parent = this;

                this.WSS.bind('PASETO_RESPONSE', async function(message) {
                    delete(message.respTopic);
                    var decodedMessage = jemNGRoot.fromObject(message);

                    if (decodedMessage.api.errorRsp) {
                        parent.locked = false;
                        return reject({
                            code: 1,
                            message: "browser not identified"
                        })
                    } else {
                        await parent.handleRspPaseto(decodedMessage.api.pasetoRsp, X25519PublicKey.from(parent.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                            .then(async rsp => {
                                if (rsp == null) {
                                    
                                } else {
                                    var pasetoMsg = Buffer.concat([rsp.payload, rsp.signature]);
                                    var pasetoToken = rsp.header.toString() + '.' + base64url(pasetoMsg);
                                    var metaKey = rsp.metaKey;

                                    var publicKey = new paseto.PublicKey.V2();
                                    var origPubKey = Buffer.from(userSigningPublicKey, 'base64');
                                    publicKey.inject(origPubKey);

                                    await V2.verify(pasetoToken, publicKey, '', async (err, data) => {
                                        if (err) {
                                            parent.locked = false;
                                            reject("Invalid username/password");
                                        } else {
                                            parent.locked = false;
                                            resolve({
                                                pasetoToken,
                                                metaKey
                                            });
                                        }
                                    });
                                }
                            });
                    }
                });
        });
    }

    async DFUUpdate(jemID, deviceKeys) {

        return new Promise(async (resolve, reject) => {

                var parent = this;

                this.WSS.bind('JEM_FIRMWARE', function(jem) {
                    parent.serviceEmitter.emit("JEM_FIRMWARE", { percent: jem.percent });
                });

                // SEND ENCRYPT REQUEST TO EVERY JEM
                await this.sendDFURequest(jemID, "device/" + deviceKeys.username + "/response");
                resolve('');
        });
    }

    async encrypt(value, deviceKeys, terminal=true) {

        return new Promise(async (resolve, reject) => {

            try {
                var jemID = await this.waitForJem();
            }
            catch(error){
                return reject({message: error})
            }

                // SEND ENCRYPT REQUEST TO EVERY JEM
                await this.sendEncReq(jemID, "device/" + deviceKeys.username + "/response", terminal, value, X25519PublicKey.from(this.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'));

                var parent = this;

                this.WSS.bind('ENCRYPT_RESPONSE', async function(message) {
                sodium = (!sodium) ? await SodiumPlus.auto() : sodium;
                    parent.serviceEmitter.emit("JEMSTATUS", "encrypting");
                    var decodedMessage = jemNGRoot.fromObject(message);
                    if (decodedMessage.api.encryptMsgRsp) {
                        await parent.handleRspEncrypt(decodedMessage.api.encryptMsgRsp, X25519PublicKey.from(parent.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                            .then(async rsp => {
                                if (rsp == null) {
                                    parent.locked = false;
                                } else {
                                    rsp = Buffer.from(rsp);
                                    parent.locked = false;
                                    resolve(rsp.toString('base64'));
                                }
                            });
                        }
            });
        });
    }

    async decrypt(value, deviceKeys) {

        return new Promise(async (resolve, reject) => {

            try {
                var jemID = await this.waitForJem();
            }
            catch(error){
                return reject({message: error})
            }

                // SEND DECRYPT REQUEST TO EVERY JEM
                await this.sendDecReq(jemID, "device/" + deviceKeys.username + "/response", true, value, X25519PublicKey.from(this.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'));

                var parent = this;

                parent.WSS.bind('DECRYPT_RESPONSE', async function(message) {
                sodium = (!sodium) ? await SodiumPlus.auto() : sodium;
                    parent.serviceEmitter.emit("JEMSTATUS", "decrypting");
                    var decodedMessage = jemNGRoot.fromObject(message);
                    if (decodedMessage.api.decryptMsgRsp) {
                        await parent.handleRspDecrypt(decodedMessage.api.decryptMsgRsp, X25519PublicKey.from(parent.jems.value.find(o => o.jemID === jemID).publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                            .then(async rsp => {
                                if (rsp == null) {
                                    parent.locked = false;
                                } else {
                                    rsp = Buffer.from(rsp);
                                    parent.locked = false;
                                    resolve(rsp.toString('base64'));
                                }
                            });
                    }
                });
        });
    }

    async startBlinkyPairing(deviceKeys) {
        this.locked = false;
        await this.sendAddClientReq(this.jems.value.find(o => o.status === 'online').jemID, "device/" + deviceKeys.username + "/response", Buffer.from(deviceKeys.encryptPublicKey, 'base64'));
    }

    async checkBlinkCodes(deviceKeys, codes) {

        var parent = this;

        return new Promise(async (resolve, reject) => {

            this.WSS.bind('SEND_ADD_CLIENT_VERIFY_RESPONSE', async function(message) {
                delete(message.respTopic);
                var decodedMessage = jemNGRoot.fromObject(message);

                if (decodedMessage.api.errorRsp) {

                } else {
                    await parent.handleRspAddClientVerify(decodedMessage.api.addClientVerifyRsp, X25519PublicKey.from(parent.jems.value.find(o => o.status === 'online').publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
                        .then(async rsp => {
                            if (rsp == null) {
                                return reject("no code");
                            } else {
                                if (rsp == 1) {
                                    // Success
                                    this.locked = false;
                                    return resolve("success");
                                } else {
                                    // Failure
                                    this.locked = false;
                                    setTimeout(async () => {
                                        await parent.sendAddClientRepeat(parent.jems.value.find(o => o.status === 'online').jemID, "device/" + deviceKeys.username + "/response");
                                    }, 5000);
                                    return reject("Incorrect code. Resending in 5 seconds...");
                                }
                            }
                        })
                }
            });

            if (codes == null) {
                await this.sendAddClientRepeat(this.jems.value.find(o => o.status === 'online').jemID, "device/" + deviceKeys.username + "/response");
            } else {
                this.sendAddClientVerify(this.jems.value.find(o => o.status === 'online').jemID, "device/" + deviceKeys.username + "/response", false, codes.red, codes.green, codes.blue, 2592000, X25519PublicKey.from(this.jems.value.find(o => o.status === 'online').publicEncryptionKey, "base64"), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'))
            }
        });
    }
    // END OF CORE FUNCTIONS --

    // DEPENDENCY FUNCTIONS --

    // Send Add Client request
    async sendAddClientReq(devID, rspTopic, pubKey) {
        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;

        var addMsg = jemNGRoot.lookupType("JEM_NG_API_AddClientReq").create();
        apiMsg.addClientReq = addMsg;
        addMsg.pubKey = pubKey;

        var err = jemNGRoot.verify(addMsg);

        this.sendApiReq("SEND_ADD_CLIENT_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    // Send Add Client repeat (blinks) request
    async sendAddClientRepeat(devID, rspTopic) {
        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;
        apiMsg.addClientRepeat = true;

        this.sendApiReq("SEND_ADD_CLIENT_REPEAT", apiMsg, ('device/' + devID + '/listen'))
    }

    // Send Add Client Verify message
    async sendAddClientVerify(devID, rspTopic, terminal, red, green, blue, duration, jemPublic, localPublic, localSecret) {
        var verMsg = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_AddClientVerify");
        var verContents = verMsg.lookupType("JEM_NG_API_AddClientVerify").create();
        verContents.verification = Buffer.from('r' + red.toString() + 'g' + green.toString() + 'b' + blue.toString())
        verContents.duration = duration;
        var err = verMsg.verify(verContents);

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = terminal;
        apiMsg.respTopic = rspTopic;
        apiMsg.addClientVerify = await this.encryptAPIReq(verMsg.encode(verContents).finish(), devID, jemPublic, localPublic, localSecret);

        this.sendApiReq("SEND_ADD_CLIENT_VERIFY", apiMsg, ('device/' + devID + '/listen'))
    }

    async handleRspAddClientVerify(rsp, jemPublic, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        // Try to decode decrypted response
        var decrypted = await this.decryptApiReq(rsp.payload, jemPublic, localPublic, localSecret);
        if (decrypted == null) {
            return;
        }

        var decodedVerRsp = null;
        try {
            decodedVerRsp = jemNGACVR.decode(decrypted);
        } catch (e) {
            return;
        }
        if (decodedVerRsp == null || decodedVerRsp.result == null) {
            return;
        }

        return decodedVerRsp.result;
    }

    async acknowledgeProvision(devID, jemPublic, localPublic, localSecret, rspTopic) {
        var ackReqMsg = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_ProvisionAck");
        var ackContents = ackReqMsg.lookupType("JEM_NG_API_ProvisionAck").create();
        ackContents.provisionFinalize = true;
        var err = ackReqMsg.verify(ackContents);
        if (err)
            throw Error(err);

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;
        apiMsg.provisionAck = await this.encryptAPIReq(ackReqMsg.encode(ackContents).finish(), devID, jemPublic, localPublic, localSecret);
        var err = jemNGRoot.verify(apiMsg);
        if (err) {
            throw Error(err);
        }
        this.sendApiReq("ACK_PROVISION", apiMsg, ('device/' + devID + '/listen'))
    }

    async handleRspProvisioningRec(rsp, jemPublic, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        var decrypted = await this.decryptApiReq(rsp.payload, jemPublic, localPublic, localSecret);
        if (decrypted == null) {}

        var decodedProvRecMsg = null;
        try {
            decodedProvRecMsg = jemNGPRR.decode(decrypted);
        } catch (e) {

        }
        if (decodedProvRecMsg == null) {
            return;
        }

        return decodedProvRecMsg;
    }

    async handleProvisioningResponse(rsp, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        var decrypted = await this.decryptApiReq(rsp.payload, environment.jemProvPubKey, localPublic, localSecret);
        if (decrypted == null) {
            return;
        }

        var decodedProvMessage = null;
        try {
            decodedProvMessage = jemNGPR.decode(decrypted);
        } catch (e) {
            return;
        }
        if (decodedProvMessage == null) {
            return;
        }

        var jemPublic = X25519PublicKey.from(decodedProvMessage.devEncPubKey);
        return {
            "jemPublicKey": jemPublic,
            "provisionResponse": decodedProvMessage
        }
    }

    async sendProvisionRecReq(devID, rspTopic, recKey, jemPublic, localPublic, localSecret) {
        var provRecReqMsg = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_ProvisionRecoveryReq");
        var recReqContents = provRecReqMsg.lookupType("JEM_NG_API_ProvisionRecoveryReq").create();
        recReqContents.userRecoveryKey = recKey;
        var err = provRecReqMsg.verify(recReqContents);
        if (err)
            throw Error(err);

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;
        apiMsg.provisionRecReq = await this.encryptAPIReq(provRecReqMsg.encode(recReqContents).finish(), devID, jemPublic, localPublic, localSecret);

        var err = jemNGRoot.verify(apiMsg);
        if (err) {
            throw Error(err);
        }

        this.sendApiReq("SEND_PROVISION_REC_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    async sendEnrollReq(devID, rspTopic) {
        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;
        apiMsg.enrollReq = jemEnrollReqEnum.ENROLL;
        this.sendApiReq("SEND_ENROLL_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    // -- END OF PROVISION FUNCTIONS --

    async sendDFURequest(devID, topic) {
        var tsMsg = jemNGRoot.lookupType("JEM_NG").create();
        tsMsg.dfu = 1;

        var err = jemNGRoot.verify(tsMsg);
        if (err)
            throw Error(err);

        this.WSS.API("SEND_DFU_UPDATE", {respTopic: 'device/' + devID + '/listen', ...tsMsg});
    }

    // -- API FUNCTIONS --
    async sendApiReq(event, req, topic) {
        var tsMsg = jemNGRoot.lookupType("JEM_NG").create();
        tsMsg.api = req;

        var err = jemNGRoot.verify(tsMsg);
        if (err)
            throw Error(err);

        this.WSS.API(event, {respTopic: topic, ...tsMsg});
    }

    async encryptAPIReq(plaintext, devID, jemPublic, localPublic, localSecret) {
        sodium = (!sodium) ? await SodiumPlus.auto() : sodium;
        var encMsg = jemNGRoot.lookupType("JEM_NG_Encrypted_API_Msg").create();
        let browserIKey, browserOKey;
        [browserIKey, browserOKey] = await sodium.crypto_kx_server_session_keys(
            localPublic,
            localSecret,
            jemPublic
        );

        let nonceBuf = await sodium.randombytes_buf(24);
        let encrypted = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
            plaintext,
            nonceBuf,
            browserOKey
        );

        var nonceMsg = Buffer.concat([nonceBuf, encrypted]);
        encMsg.payload = nonceMsg;
        encMsg.clientPubKey = localPublic.getBuffer();

        return encMsg;
    }

    async decryptApiReq(payload, jemPubKey, localPubKey, localPrivKey) {
        sodium = (!sodium) ? await SodiumPlus.auto() : sodium;
        var msgBuf = Buffer.from(payload);
        var nonceBuf = msgBuf.slice(0, 24);
        var justMsgBuf = msgBuf.slice(24);
        let browserIKey, browserOKey;
        [browserIKey, browserOKey] = await sodium.crypto_kx_server_session_keys(
            localPubKey,
            localPrivKey,
            jemPubKey
        );

        try {
            let decrypted = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
                justMsgBuf,
                nonceBuf,
                browserIKey
            );
            return decrypted;
        } catch (e) {
            return null;
        }
    }
    // -- END OF API FUNCTIONS --

    async handleRspEncrypt(rsp, jemPublic, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        // Try to decode decrypted response
        var decrypted = await this.decryptApiReq(rsp.payload, jemPublic, localPublic, localSecret);
        if (decrypted == null) {
            return;
        }

        var decodedEncRsp = null;
        try {
            decodedEncRsp = jemNGER.decode(decrypted);
        } catch (e) {
            return;
        }
        if (decodedEncRsp == null || decodedEncRsp.kekEnc == null) {
            return;
        }

        return decodedEncRsp.kekEnc;

    }
    async handleRspDecrypt(rsp, jemPublic, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        // Try to decode decrypted response
        var decrypted = await this.decryptApiReq(rsp.payload, jemPublic, localPublic, localSecret);
        if (decrypted == null) {
            return;
        }

        var decodedDecRsp = null;
        try {
            decodedDecRsp = jemNGDR.decode(decrypted);
        } catch (e) {
            return;
        }
        if (decodedDecRsp == null || decodedDecRsp.kek == null) {
            return;
        }

        return decodedDecRsp.kek;

    }

    // Send PASETO Token request
    async sendPasetoReq(devID, rspTopic, terminal, aud, duration, access, jemPublic, localPublic, localSecret) {
        var pasetoMsg = jemNGRoot.lookupType("JEM_NG_API_PasetoReq");
        var pContents = pasetoMsg.lookupType("JEM_NG_API_PasetoReq").create()
        pContents.aud = aud;
        pContents.duration = duration;
        pContents.accessLevel = access;

        var err = pasetoMsg.verify(pContents);
        if (err) {
            throw Error(err);
        }

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = terminal;
        apiMsg.respTopic = rspTopic;
        apiMsg.pasetoReq = await this.encryptAPIReq(pasetoMsg.encode(pContents).finish(), devID, jemPublic, localPublic, localSecret);
        this.sendApiReq("PASETO_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    async handleRspPaseto(rsp, jemPublic, localPublic, localSecret) {
        if (rsp.payload == null) {
            return;
        }

        // Try to decode decrypted response
        var decrypted = await this.decryptApiReq(rsp.payload, jemPublic, localPublic, localSecret);
        if (decrypted == null) {
            return;
        }

        var decodedPasetoRsp = null;
        try {
            decodedPasetoRsp = jemNGPsR.decode(decrypted);
        } catch (e) {
            return;
        }
        if (decodedPasetoRsp == null || decodedPasetoRsp.header == null || decodedPasetoRsp.payload == null || decodedPasetoRsp.signature == null) {
            return;
        }
        return decodedPasetoRsp;
    }

    async sendEncReq(devID, rspTopic, terminal, kek, jemPublic, localPublic, localSecret) {
        var encReqMsg = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_EncryptKeyReq");
        var encContents = encReqMsg.lookupType("JEM_NG_API_EncryptKeyReq").create();
        encContents.kek = kek;

        var err = encReqMsg.verify(encContents);
        if (err) {
            throw Error(err);
        }

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = terminal;
        apiMsg.respTopic = rspTopic;
        apiMsg.encryptMsgReq = await this.encryptAPIReq(encReqMsg.encode(encContents).finish(), devID, jemPublic, localPublic, localSecret);
        this.sendApiReq("ENCRYPT_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    async sendDecReq(devID, rspTopic, terminal, kekEnc, jemPublic, localPublic, localSecret) {
        var decReqMsg = protobuf.Root.fromJSON(jemNGjson).lookupType("JEM_NG_API_DecryptKeyReq");
        var decContents = decReqMsg.lookupType("JEM_NG_API_DecryptKeyReq").create();
        decContents.kekEnc = Buffer.from(kekEnc);

        var err = decReqMsg.verify(decContents);
        if (err) {
            throw Error(err);
        }

        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = terminal;
        apiMsg.respTopic = rspTopic;
        apiMsg.decryptMsgReq = await this.encryptAPIReq(decReqMsg.encode(decContents).finish(), devID, jemPublic, localPublic, localSecret);
        this.sendApiReq("DECRYPT_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

    async sendProvisionReq(devID, rspTopic, email, pubKey, duration) {
        var apiMsg = jemNGRoot.lookupType("JEM_NG_API").create();
        apiMsg.terminal = false;
        apiMsg.respTopic = rspTopic;

        var provMsg = jemNGRoot.lookupType("JEM_NG_API_ProvisionReq").create();
        apiMsg.provisionReq = provMsg;
        provMsg.jemEmailAddress = email;
        provMsg.pubKey = pubKey;
        provMsg.duration = duration;

        var err = jemNGRoot.verify(provMsg);
        if (err) {
            throw Error(err);
        }

        this.sendApiReq("SEND_PROVISION_REQUEST", apiMsg, ('device/' + devID + '/listen'))
    }

}