import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SodiumPlus, X25519PublicKey, X25519SecretKey, CryptographyKey } from 'sodium-plus';
import { JemService } from '../../../shared/services/jem.service';
import { AuthService } from '../../../shared/services/auth.service';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

import { Formio, FormioAppConfig, FormioSubmission, FormioUtils} from '@formio/angular';



// (Formio as any).icons = 'fontawesome'; 
