@Component({
  selector: 'app-cred-detail-in-modal',
  templateUrl: './cred-detail-in-modal.component.html',
  styleUrls: ['./cred-detail-in-modal.component.scss']
})
export class CredDetailInModalComponent implements OnInit, OnDestroy {

  myForm:any = require ('./credentialForm.json'); 

  @Input() credential: any;
  // loading = false;
  loadingText: string;
  decrypting = false;
  saving = false;
  // TODO: check whether changing the type of loading and loading text from array to any,
  // which was required to get this component working, could cause problems.
  // loading: any[] = [];
  // loadingText: any[] = [];
  viewMode: 'edit' | 'print' = 'print';
  id: String;
  isNew: boolean;
  vault: any = {};
  vaultForm: FormGroup;

  stateData = {
    "isModeEdit": false, 
    "isMfaConfigured": false, 
    "isDecrypted": false,  
    "showPasswordGenerator": false, 
    "decrypting": false, 
    "saving": false 
  }; 

  decryptedData = {
    "password": "Plaintext Password", 
    "mfaCode": "Your MFA Code"
  }; 

  CHARACTER_SETS = [
    ["Numbers", "0123456789"],
    ["Lowercase", "abcdefghijklmnopqrstuvwxyz"],
    ["Uppercase", "ABCDEFGHIJKLMNOPQRSTUVWXYZ"],
    ["ASCII symbols", "!\"#$%&'()*+,-./:;<=>?@[\\]^_`{|}~"]
  ];

  showPasswordGenerator:boolean = false;

  showJemPulsingGreenInfoAlert: boolean = false; 

  activeCredTitle: string;

  constructor(
    public activeModal: NgbActiveModal,
    private jemService: JemService,
    private auth: AuthService,
    private fb: FormBuilder,
    private toastr: ToastrService

  ) { }


  ngOnInit() {
    
    this.decrypting = this.stateData.decrypting; 
    this.saving = this.stateData.saving; 
    var parent = this;

    Formio.createForm(document.getElementById("myForm"), this.myForm)
    .then(function(form) {

      // Populate the form with credential data 
      form.submission = { data: 
        {
          ...parent.stateData,
          ...parent.credential.metaData, 
          ...parent.credential.passData 
        }
      };

      // Prevent the submission from going to the form.io server.
      form.nosubmit = true;

      // Handle form button clicks - buttons emit eponymous events
      form.on('customEvent', (emittedEvent) => {
        switch (emittedEvent.type) {

          case "addMfaConfigButtonClick":
            console.log(emittedEvent.type)
          break;

          case "deleteMfaConfigButtonClick":
            console.log(emittedEvent.type)
          break;

          case "editCredButtonClick":
            console.log(emittedEvent.type)
            // parent.credential.isModeEdit = true; 
            // form.submission = { data: parent.credential}; 
          break;

          case "decryptCredButtonClick":
            console.log(emittedEvent.type);
          break;

          case "saveCredButtonClick":
            console.log(emittedEvent.type)
          break;

          case "submitCredButtonClick":
            console.log(emittedEvent.type)
          break;

          case "passwordFieldButtonClick": 

            const passwordFieldButton = FormioUtils.getComponent(form.components, 'passwordFieldButton', true).component; 
            
            // If not decrypted, decrypt cred (or simulate decryption)             
            if (!form.data.isDecrypted) { 

              // Set decryping to true; this will cause the form to display the "Press JEM" message 
              parent.stateData.decrypting = true; 
              form.submission = { data: 
                { ... parent.stateData, 
                  ... parent.credential.metaData, 
                  ... parent.decryptedData
                } 
              }; 
              
              // Simulate use of JEM and decrypting 
              setTimeout (() => {

                parent.decryptedData = {                
                  "password": "Plaintext Password", 
                  "mfaCode": "Your MFA Code"
                }; 

                parent.stateData.isDecrypted = true;
                parent.stateData.decrypting = false; 

                // Spread the modified stateData and newly decrypted passData into the form 
                form.submission = { data: 
                  { ... parent.stateData, 
                    ... parent.credential.metaData, 
                    ... parent.decryptedData
                  } 
                }; 

                // Set passwordFieldButton label & suffix to display decrypted password, etc. 

                if (parent.decryptedData.password) {
                  passwordFieldButton.label = parent.decryptedData.password; 
                  passwordFieldButton.rightIcon = "iconsmind i-Duplicate-Layer";
                  passwordFieldButton.theme = "warning"; 
                } else {
                  passwordFieldButton.label = "No saved password for this record.";
                  passwordFieldButton.rightIcon = "";
                  passwordFieldButton.theme = "warning"; 
                } 
                }, 5000);

              // Get passData from API, decrpyt it and spread decrypted data into form 

              // Redraw the form
              form.redraw();

              // Clear decrypted data after 10s
              setTimeout(() => {
                passwordFieldButton.label = "Encrypted: Click to Decrypt";
                passwordFieldButton.rightIcon = "";
                passwordFieldButton.theme = "success"; 
                parent.decryptedData = {
                    "password": '', 
                    "mfaCode": ''
                  };
                form.submission = { data: {isDecrypted: false}}

                // Redraw the form
                form.redraw();

              }, 20000);

            // If cred is already decrypted, copy plaintext password to clipboard 
            } else {              

              // Replace this line with a call to copy() 
              console.log("copy plaintext password to clipboard"); 

              // Provide user feedback 
              passwordFieldButton.label = (parent.credential.passData.decryptedPassword) ? "Copied password to clipboard" : "No saved password for this record.";
              setTimeout(() => {
                passwordFieldButton.label = (parent.credential.passData.decryptedPassword) ? parent.credential.passData.decryptedPassword : "No saved password for this record.";
              }, 1500);

            }

          break;

      }          
      
      });

    });


    this.id = this.credential.id;
    this.activeCredTitle = this.credential.title; 
    this.isNew = !this.id;
    this.buildVaultForm(this.credential);
  }

  ngOnDestroy() {
    if(this.credential.id) {
      this.credential.eraseDecryptedData(0);
    }    
  }

  async decrypt(credential, passKEK, passData) {
    this.loadingText = "Press your JEM...";
    this.decrypting = true;
    var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    var passKEKBuf = Buffer.from(passKEK, 'base64');
    var passKeyB64;

    try {
      await this.jemService.decrypt(passKEKBuf, this.auth.deviceSettings)
          .then(async passKey => {
        passKeyB64 = passKey;
      });
    }
    catch(error) {
      this.decrypting = false;
      this.toastr.error(error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }
    var passDataBuf = Buffer.from(passData, 'base64');

    var passData = await sodium.crypto_aead_xchacha20poly1305_ietf_decrypt(
    passDataBuf.slice(24),
    passDataBuf.slice(0, 24),
    CryptographyKey.from(passKeyB64, 'base64'));

    var passData = JSON.parse(passData.toString());
    credential.status = 'decrypted';
    credential.decryptedPassword = passData.password;
    this.decrypting = false;

    this.buildVaultForm(this.credential);

    credential.eraseDecryptedData(30000);
  }

  isMobile() {
    return window.innerWidth < 600;
  }

  buildVaultForm(i: any = {}) { 
    this.vaultForm = this.fb.group({
        id: [i.id],
        title: [i.title],
        url: [i.url],
        username: [i.username],
        status: [i.status], 
        password: [i.decryptedPassword],
        length: 14,
        numbers: 1,
        uppercase: 1,
        lowercase: 1,
        specialchars: 1
    });
  }

  createItem(item: any = {}) {
    return this.fb.group({
        title: [item.title],
        url: [item.url],
        username: [item.username],
        password: [item.password]
    });
  }

  async saveItem(terminal = true) {
    return new Promise(async (resolve, reject) => {
      this.saving = true;
      this.loadingText = 'Touch your JEM...';
      var sodium = (!sodium) ? await SodiumPlus.auto() : sodium;
      var metaKey = await sodium.randombytes_buf(32);
      var passKey = await sodium.randombytes_buf(32);
      this.vault = this.vaultForm.value;
      this.vault = {...this.credential, ...this.vaultForm.value};
  
      var record:any = {
          metaKEK: "",
          passKEK: "",
          type: 1,
          metaData: {
              "title": this.vault.title,
              "username": this.vault.username,
              "url": this.vault.url
          },
          passData: {
              "password": this.vault.password
          }
      };
  
      // ENCRYPT METADATA
      var nonce = await sodium.randombytes_buf(24);
      record.metaData = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
          JSON.stringify(record.metaData),
          nonce,
          CryptographyKey.from(metaKey)
      );
      record.metaData = Buffer.concat([nonce, record.metaData]);
      record.metaData = record.metaData.toString('base64');
  
      // if (this.vault.password !== null) {
        // ENCRYPT PASSDATA
        var nonce = await sodium.randombytes_buf(24);
        record.passData = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
            JSON.stringify(record.passData),
            nonce,
            CryptographyKey.from(passKey)
        );
        record.passData = Buffer.concat([nonce, record.passData]);
        record.passData = record.passData.toString('base64');
      // } else {
      //   record.passData = this.vault.passData;
      // }
  
      // ENCRYPT METAKEY
      var nonce = await sodium.randombytes_buf(24);
      record.metaKEK = await sodium.crypto_aead_xchacha20poly1305_ietf_encrypt(
          metaKey,
          nonce,
          CryptographyKey.from(this.auth.userSettings.metaKey)
      );
      record.metaKEK = Buffer.concat([nonce, record.metaKEK]);
      record.metaKEK = record.metaKEK.toString('base64');
  
      try {
        await this.jemService.encrypt(passKey, this.auth.deviceSettings, terminal)
        .then(data => {
            record.passKEK = data;
        });
      } catch (error) {
        this.saving = false;
        this.toastr.error(error.message, 'Error', { 
          timeOut: 5000, 
          closeButton: true, 
          progressBar: true  
        });
        return reject();
      }
  
      if(!this.credential.id) {      
        await this.auth.addCredential(record)
                  .subscribe((item: any) => {
                      this.toastr.success('Saved item!', 'Success!', { 
                        timeOut: 3000 });
                        this.activeModal.close('OK');
                  });
                resolve ('');
        } else {
          await this.auth.updateCredential(this.credential.id, record)
          .subscribe((item: any) => {
              this.toastr.success('Item Saved!', 'Success!', { timeOut: 3000 });
              this.activeModal.close('OK');
          });
          resolve('');
        }
      });
  }


  async editCredential(credential) {
    if(credential.status == "encrypted") {
      await this.decrypt(credential, credential.passKEK, credential.passData); 
    }
    credential.clearEraseDecryptedDataTimeout();
    this.viewMode = 'edit'
    
  }

    // PASSWORD CREATION

    async getRandomInt(min, max) {
      var sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
      var byteArray = await sodium.randombytes_buf(1);
      var range = max - min + 1;
      var max_range = 256;
      if (byteArray[0] >= Math.floor(max_range / range) * range)
          return this.getRandomInt(min, max);
      
      return min + (byteArray[0] % range);
    }
  
    doCopy() {
        const selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = this.vaultForm.value.password;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
        this.toastr.success('The password is in your clipboard!', 'Success!', { timeOut: 5000 });
    }
    
    async random_str()
    {
        var pieces = [];
        var length = this.vaultForm.value.length;
        var keyspace = '';
        keyspace = (this.vaultForm.value.numbers == 1) ? this.CHARACTER_SETS[0][1] : '';
        keyspace += (this.vaultForm.value.lowercase == 1) ? this.CHARACTER_SETS[1][1] : '';
        keyspace += (this.vaultForm.value.uppercase == 1) ? this.CHARACTER_SETS[2][1] : '';
        keyspace += (this.vaultForm.value.specialchars == 1) ? this.CHARACTER_SETS[3][1] : '';
        var max = (keyspace.length- 1);
        for (var i = 0; i < length; ++i) {
            pieces.push(keyspace[await this.getRandomInt(0,max)]);
        }
        this.vaultForm.patchValue({password: pieces.join("")});
    }
}
