'use strict';
import {
    Injectable
} from "@angular/core";
import { environment } from 'src/environments/environment';
import { webSocket } from 'rxjs/webSocket';
import { EventEmitter } from "events";

@Injectable({
    providedIn: 'root',
})
export class WebSocketService {

    private APISocket:any;
    private callbacks = {};
    public event = new EventEmitter();

async connectToAPI(username, emailAddress) {

    var parent = this;

    function subscribeToAPI() {

        parent.APISocket = webSocket(environment.WSSEndpoint);

        parent.APISocket.subscribe(
            (message => {
                parent.dispatch(message.event, message.data)
            }), // Called whenever there is a message from the server.
            err => {
                // subscribeToAPI();
                parent.event.emit('socket_reconnected');
            }, // Called if at any point WebSocket API signals some kind of error.
            () => {
                parent.callbacks = {};
                } // Called when connection is closed (for whatever reason).
            );

            parent.API('browser_login', 
            {
                browserUser: username,
                emailAddress: emailAddress
            });
    }

    subscribeToAPI();
}

async disconnectAPI(removeCallbacks=true) {
    if(this.APISocket) {
        this.APISocket.complete();
    }
    if(removeCallbacks) this.callbacks = {};
}

bind(event_name, callback){
    this.callbacks[event_name] = this.callbacks[event_name] || [];
    this.callbacks[event_name].push(callback);
};

dispatch(event_name, message){
    var chain = this.callbacks[event_name];
    if(typeof chain == 'undefined') return; // no callbacks for this event
    for(var i = 0; i < chain.length; i++){
      chain[i]( message )
    }
    if((event_name !== 'JEMSTATUS') && (event_name !== 'JEM_FIRMWARE') && (event_name !== 'getAllCredentials_RESPONSE')) delete(this.callbacks[event_name]);
} 

API(event_name, event_data){
    var payload = {event:event_name, data: event_data};
    this.APISocket.next( payload ); // <= send JSON data to socket server
    return this;
};

}